/* Home Styles (main) */

/* 320px — 480px: Mobile devices.
481px — 768px: iPads, Tablets.
769px — 1024px: Small screens, laptops. */


*{
	margin: 0;
	padding: 0;
	list-style: none;
	text-decoration: none;
	border: none;
	outline: none;
  font-family: 'Roboto', sans-serif;
}

body {
  background-color: black; 
}


.Home_main__16Znb {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 80vh;  
}

.Home_logo__3auvB {
