/* Nav Style */

.Nav_navList__2uOCT {
    width: 100%;
    height: 50px;
    background-color: rgba(255, 255, 255, 0.959);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    overflow: hidden;
    position: fixed;
    font-size: 1.5em;
}

a:link {
    color: black;
}

a:hover {
    color: rgb(63, 63, 63);
    text-decoration: underline;
    text-shadow: #838383 0 0 8px;
}
a:active{
    color: rgba(133, 0, 0, 0.431);
}

a:visited{
    color: black;
}

@media  (max-width: 1064px) {
    .Nav_navList__2uOCT {
        display: none;
    }
}

.Nav_navItem__1C5nY {
    cursor: pointer;
}
